import React, {useDebugValue, useState} from 'react';
import './App.css';
import { Route, Routes} from "react-router-dom";
import Home from "./pages/Home";
import NavBar from "./components/Navbar";
import {useAppSelector} from "./store";
import {createTheme, ThemeProvider} from "@mui/material/styles";
import {Box, SpeedDial, SpeedDialIcon} from '@mui/material';
import Copyright from "./components/Copyright";
import ModalBarcodeScanner from "./components/ModalBarcodeScanner";

function App() {
    const mode = useAppSelector(state => state.theme.mode);
    const [barcodeScanner, setBarcodeScanner] = useState<boolean>(false)
    const theme = React.useMemo(() => createTheme({
        palette: {
            mode,
        },
    }), [mode]);
    return (
        <ThemeProvider theme={theme}>
            <NavBar/>
            {barcodeScanner && <ModalBarcodeScanner onClose={() => setBarcodeScanner(!barcodeScanner)}/>}
            {!barcodeScanner && <SpeedDial
                onClick={() => setBarcodeScanner(!barcodeScanner)}
                ariaLabel="SpeedDial basic example"
                sx={{ position: 'absolute', bottom: 16, right: 16 }}
                icon={<span className="fa-duotone fa-barcode-read"/>}
            />}
            <Box
                component="main"
                sx={{
                    backgroundColor: (theme) =>
                        theme.palette.mode === 'light'
                            ? theme.palette.grey[100]
                            : theme.palette.grey[900],
                    flexGrow: 1,
                    height: '100vh',
                    overflow: 'auto',
                }}
            >
            <Routes>
                <Route path="/" element={<Home/>}/>
            </Routes>
            </Box>
            <Copyright sx={{position: 'absolute', bottom: 16, left: 16 }} />
        </ThemeProvider>

    );
}


export default App;
